import React, {Component} from 'react';
// CSS
import '../css/reset.css';
import 'normalize.css';
import '../css/App.css';
// Component
import TodoInput from './TodoInput';
import TodoItem from './TodoItem';
import UserDialog from './UserDialog';
import AV, {getCurrentUser, signOut, TodoModel} from "./leanCloud";
import LeftAside from "./LeftAside"

// Component App
class App extends Component {
    constructor(props) {
        super(props);
        // 
        this.state = {
            user: getCurrentUser() || {},
            newTodo: '',
            todoList: [],
            targetBar: "unfinished",
        };
        //  ==> 
        this.getUserInfo();
    }

    render() {
        console.log("App render");
        // console.log(this.state.todoList);

        // ===============================    =====================================
        let Unfinished = this.createTags(this.filterUnfinished());
        let Finished = this.createTags(this.filterFinished());
        let Deleted = this.createTags(this.filterDeleted());
        // ===============todos===============
        let todos = this.setTags(Unfinished, Finished, Deleted);

        // 
        return (
            <div className="App">
                <aside>
                    <LeftAside
                        user={this.state.user}
                        changeBars={this.changeBars.bind(this)}
                    />
                    {this.state.user.id ?
                        <button id="signOut" onClick={this.signOut.bind(this)}>
                            <span>&#xe65f;</span>
                            <span>signout</span>
                        </button> :
                        null}
                </aside>
                <main>
                    <h1>
                        {this.state.user.username || ""}
                    </h1>
                    <div className="inputWrapper">
                        {/*
                         ** content  newTodo
                         ** onSubmit  addTodo
                         ** onChange  changeTitle
                         **/}
                        <TodoInput content={this.state.newTodo}
                                   onSubmit={this.addTodo.bind(this)}
                                   onChange={this.changeTitle.bind(this)}/>
                    </div>
                    {/*  Todos */}
                    <ol className='todoList'>
                        {todos}
                        {/*{Unfinished}*/}
                        {/*{Finished}*/}
                        {/*{Deleted}*/}
                    </ol>
                    {this.state.user.id ?
                        null :
                        <UserDialog onSignUp={this.onSignUpOrSignIn.bind(this)}
                                    onSignIn={this.onSignUpOrSignIn.bind(this)}/>}
                </main>
            </div>
        );
    }

    //getUserInfo 
    getUserInfo() {
        let user = getCurrentUser();
        if (user) {
            TodoModel.getByUser(user, (todos) => {
                let stateCopy = JSON.parse(JSON.stringify(this.state));
                stateCopy.todoList = todos;
                this.setState(stateCopy)
            })
        }
    }

    // /
    onSignUpOrSignIn(user) {
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.user = user;
        this.setState(stateCopy);
        // 
        this.getUserInfo();
    }

    //  ===> 
    signOut() {
        signOut();
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.user = {};
        stateCopy.todoList = [];
        this.setState(stateCopy);
    }

    // componentDidUpdate 
    componentDidUpdate() {
    }

    //  TodoItem
    delete(event, todo) {
        let oldDeleted = todo.deleted;
        todo.deleted = todo.deleted === false;
        this.setState(this.state);
        TodoModel.update(todo, () => {
                this.setState(this.state)
            }, (error) => {
                todo.status = oldDeleted;
                this.setState(this.state);
                alert("")
            }
        );
    }

    //  TodoItem  ==> leanCloud
    toggle(e, todo) {
        let oldStatus = todo.status;
        todo.status = todo.status === 'completed' ? '' : 'completed';
        this.setState(this.state);
        TodoModel.update(todo, () => {
                this.setState(this.state)
            }, (error) => {
                todo.status = oldStatus;
                this.setState(this.state);
                alert("")
            }
        );
    }

    // TotoInput
    changeTitle(event) {
        this.setState({
            newTodo: event.target.value,
            todoList: this.state.todoList
        })
    }

    // TodoListTodo
    addTodo(event) {
        if (event.target.value.trim() !== "") {
            let newTodo = {
                status: "",
                title: event.target.value,
                deleted: false
            };
            TodoModel.create(newTodo, (id) => {
                // leanCloud.js ==> successFn(response.id)
                newTodo.id = id;
                this.state.todoList.push(newTodo);
                this.setState({
                    newTodo: "",
                    todoList: this.state.todoList
                })
            }, (error) => {
                // leanCloud.js ==> errorFn(error)
                console.log(error)
            });
        } else {
            alert("")
        }
        // console.log(this.state.todoList)
    }

    //==================================================================================

    // 

    filterUnfinished() {
        let Unfinished = [];
        this.state.todoList.forEach(e => {
            if (e.status === "" && e.deleted === false) {
                Unfinished.push(e);
            }
        });
        return Unfinished;
    }

    // 
    filterFinished() {
        let finidhed = [];
        this.state.todoList.forEach(e => {
            if (e.status !== "" && e.deleted === false) {
                finidhed.push(e);
            }
        });
        return finidhed;
    }

    // 

    filterDeleted() {
        let deleted = [];
        this.state.todoList.forEach(e => {
            if (e.deleted) {
                deleted.push(e)
            }
        });
        return deleted
    }

    //===============================================================
    createTags(todos) {
        return todos.map((e, i) => {
            return (
                <li key={i}>
                    <TodoItem todo={e}
                              onToggle={this.toggle.bind(this)}
                              onDelete={this.delete.bind(this)}/>
                </li>
            )
        })
    }

    setTags(unfinished, finished, deleted) {
        if (this.state.targetBar === "unfinished") {
            return unfinished;
        } else if (this.state.targetBar === "finished") {
            return finished;
        } else {
            return deleted;
        }
    }

    // =============================check selectedAsideBar================================
    changeBars(target) {
        // console.log(target);
        if (target.classList.contains("unfinished")) {
            this.setState({
                targetBar: "unfinished"
            });
        } else if (target.classList.contains("finished")) {
            this.setState({
                targetBar: "finished"
            });
        } else {
            this.setState({
                targetBar: "recycle"
            });
        }
    }

}

// 
export default App;





