import React from 'react';
import '../css/TodoInput.css';

// Component TodoInput
export default function (props) {
    //<App/>value
    return (
        <input type="text"
               className="TodoInput"
               placeholder=""
               value={props.content}
               onKeyPress={submit.bind(null, props)}
               onChange={changeTitle.bind(null, props)}/>
    )
}

// []  <App/>  todoList
function submit(props, e) {
    if (e.key === 'Enter') {
            props.onSubmit(e)
    }
}

//  <App/>  input
function changeTitle(props, e) {
    props.onChange(e)
}



