import React, {Component} from "react";
// CSS
import "../css/UserDialog.css";
// leanCloud
import {signUp, signIn, sendPasswordResetEmail} from "./leanCloud";
//  / 
import SignInOrSignUp from "./SignInOrSignUp"
//  
import ForgetPasswordForm from "./ForgetPasswordForm";
// 
import {VerifyData} from "./VerifyData"

// Component UserDialog
export default class UserDialog extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: "signInOrSignUp",
            formData: {
                email: '',
                username: '',
                password: ''
            }
        }
    }


    // 
    signUp(e) {
        e.preventDefault();
        let {email, username, password} = this.state.formData;

        // 
        if (!(VerifyData.isEmail(email) && VerifyData.isUsername(username) && VerifyData.isPassword(password))) {
            return;
        }
        let success = (user) => {
            this.props.onSignUp.call(null, user);
            console.log(user)
        };
        let error = (error) => {
            console.log(error.code);
            switch (error.code) {
                case 125:
                    alert("");
                    break;
                case 200:
                    alert("");
                    break;
                case 201:
                    alert("");
                    break;
                case 202:
                    alert("");
                    break;
                case 217:
                    alert("");
                    break;
                case 218:
                    alert("");
                    break;
                default:
                    console.log(error);
                    alert("");
                    break;
            }
        };
        signUp(email, username, password, success, error)
    }

    // 
    signIn(e) {
        e.preventDefault();
        let {username, password} = this.state.formData;
        if (!(VerifyData.isUsername(username) && VerifyData.isPassword(password))) {
            return;
        }
        let success = (user) => {
            this.props.onSignIn.call(null, user)
        };
        let error = (error) => {
            // console.log(error.code);
            switch (error.code) {
                case 200:
                    alert("");
                    break;
                case 201:
                    alert("");
                    break;
                case 210:
                    alert("");
                    break;
                case 211:
                    alert("");
                    break;
                case 217:
                    alert("");
                    break;
                case 218:
                    alert("");
                    break;
                case 219:
                    alert("15");
                    break;

                default:
                    console.log(error);
                    alert("");
                    break;
            }

        };
        signIn(username, password, success, error)
    }

    changeFormData(key, e) {
        // this.stateformData
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.formData[key] = e.target.value;
        this.setState(stateCopy);
    }

    render() {

        return (
            <div className="UserDialog-Wrapper">
                <div className="UserDialog">
                    {/*  / */}
                    {this.state.selectedTab === "signInOrSignUp" ?
                        <SignInOrSignUp formData={this.state.formData}
                                        onSignIn={this.signIn.bind(this)}
                                        onSignUp={this.signUp.bind(this)}
                                        onChange={this.changeFormData.bind(this)}
                                        onForgetPassword={this.showForgetPassword.bind(this)}/> :
                        <ForgetPasswordForm formData={this.state.formData}
                                            onSubmit={this.resetPassword.bind(this)}
                                            onChange={this.changeFormData.bind(this)}
                                            onSignIn={this.returnToSignIn.bind(this)}/>}
                </div>
            </div>
        )
    }

    // 
    showForgetPassword() {
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.selectedTab = "forgetPassword";
        this.setState(stateCopy);
    }

    resetPassword(e) {
        e.preventDefault();
        sendPasswordResetEmail(this.state.formData.email);
        alert("")
    }

    // 
    returnToSignIn() {
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.selectedTab = "signInOrSignUp";
        this.setState(stateCopy);
    }
}



